:root {
  --toggle-bg-start: hsl(230, 22%, 74%);
  --toggle-bg-end: hsl(230, 22%, 74%);
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youTube: hsl(348, 97%, 39%);
  --main-bg: hsl(0, 0%, 100%);
  --main-bg-top: hsl(225, 100%, 98%);
  --card-bg: hsl(227, 47%, 96%);
  --text-color: hsl(228, 12%, 44%);
  --figure-values: hsl(230, 17%, 14%);
}

[data-theme=dark] {
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --LimeGreen: hsl(163, 72%, 41%);
  --BrightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youTube: hsl(348, 97%, 39%);
  --main-bg: hsl(230, 17%, 14%);
  --main-bg-top: hsl(232, 19%, 15%);
  --card-bg: hsl(228, 28%, 20%);
  --text-color: hsl(228, 34%, 66%);
  --figure-values: hsl(0, 0%, 100%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

button {
  cursor: pointer;
}

ul,
ol {
  list-style-type: none;
}

body {
  font-family: var(--font-inter);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 2rem;
}

a {
  text-decoration: none;
}